*{
    margin: 0;
    padding: 0;
    font-family: Georgia, 'Times New Roman', Times, serif;
    box-sizing: border-box;
}

body{
    background: linear-gradient(90deg , rgb(4, 85, 85), rgb(80, 11, 11), rgb(68, 4, 68));
    padding: 20px;
}

.weather{
    width: 800px;
    padding: 10px;
    margin: 40px auto 50px;
    background: linear-gradient(45deg, red, cyan);
    border-radius: 24px;
    
}

.weather h1{
    text-align: center;
    font-size: 40px;
    font-weight: 600;
    color: rgba(255, 255, 255, 1);
    

}

.input_value{
    width: 100%;
    text-align: center;
    margin: 40px 0 ;

}

.input_value input{
    width: 38%;
    height: 45px;
    margin-right: 20px;
    font-size: 23px;
    font-weight: 500;
    padding: 0 14px;
    border: 0;
    border-radius: 14px;
    outline-color: white;
    box-shadow: 7px 8px 25px black;
    background-color: rgba(255, 255, 255, 0.433);
    color: white;
}

::placeholder{
    color: white;
}

.input_value #btn{
    width: 12%;
    height: 45px;
    font-size: 23px;
    font-weight: 500;
    border-radius: 14px;
    background: linear-gradient(45deg, rgba(180, 17, 244, 0.427), rgba(222, 29, 29, 0.526));
    color: white;

}

.city-temp{
    width: 100%;
    display: none;

}

.temp-info , .wind-info{
    width: 100%;
    display: flex;
    justify-content: space-around;
    text-align: center;

}
.image-div{
   width: 46%;
  
}


img{
    width: 75%;
    height: 220px;
    object-fit: cover;
    object-position: center;
    border-radius: 10px;
    margin:10px 0 ;
/*     box-shadow: 7px 6px 22px black;
 */    

}

h2{
    width: 84%;
    margin: 0 0 20px 34px;
    height: 60px;
    padding-top: 14px;
    border-radius: 20px;
    color: white;
    font-weight: 600;
    font-size: 22px;
    background: linear-gradient(45deg, rgb(9, 86, 86), rgb(102, 13, 13), rgb(19, 19, 132));
}

#wind_flow{
    animation: wind 3s infinite alternate-reverse;
}

#humidity{
    animation: humidity 2s infinite alternate-reverse;
}

.error{
    animation: shake .1s linear 10 ;
}

@keyframes wind{
    0%{
        transform: translateX(0);
    }
    100%{
        transform: translateX(90px);
    }
   
}


@keyframes humidity{
    0%{
        transform: translateY(0);
    }
    100%{
        transform: translateY(-20px);
    }
   
}


@keyframes shake{
    0%{
        transform: translateX(0);
    }
    25%{
        transform: translateX(-2px);
    }

    
    50%{
        transform: translateX();
    }

    75%{
        transform: translateX(2px);
    }
    100%{
        transform: translateX(0);
    }
   
}